import { observer } from "mobx-react-lite";
import { useParams } from "react-router-dom";
import store from "@/MST/store";

import styles from "./ProjectPage.module.scss";
import { useEffect, useState } from "react";
import { UserProjectType } from "@/types/common";

const ProjectPage: React.FC = () => {
  const { id }: any = useParams();
  const [currentProject, setCurrentProject] =
    useState<UserProjectType | null>();
  const { getProjectById } = store.user;

  useEffect(() => {
    const project = getProjectById(id);
    setCurrentProject(project);
  }, []);

  return currentProject ? (
    <div className={styles.projectPage}>{currentProject._id}, {currentProject.projectName}, {currentProject.projectURL}</div>
  ) : (
    <div>Project not found.</div>
  );
};

export default observer(ProjectPage);
