import { UserProjectType } from "@/types/common";
import { memo } from "react";
import { Link, useRouteMatch } from "react-router-dom";
import styles from "./ProjectTile.module.scss";

const ProjectTile: React.FC<Partial<UserProjectType>> = ({
  _id,
  projectName,
  projectCreationDate,
  projectURL,
}) => {

  const { url } = useRouteMatch();
  console.log('ProjectTile render')
  return (
    <Link to={`${url}/${_id}`}>
      {" "}
      <div className={styles.projectTile}>
        <div className={styles.projectName}>{projectName}</div>
        <div className={styles.projectURL}>URL: {projectURL}</div>
        <div className={styles.projectURL}>Created: {projectCreationDate}</div>
      </div>
    </Link>
  );
};

export default memo(ProjectTile);
